<template>
    <v-container>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row >
                <!--<v-col></v-col>-->
                <v-col class="text-center"><v-toolbar-title>{{chatsText}}</v-toolbar-title></v-col>
                <!-- <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small style="display: inline;" text color="primary"
                                                                                            @click="openProfileEdit">{{editText}}
                 </v-btn></v-col>-->
            </v-row>


        </v-app-bar>

        <v-row style="margin-top: 54px">
            <v-col class="pa-0">
                <v-list three-line class="ma-0 pa-0">
                    <template v-for="(item, index) in items">
                        <v-subheader
                                v-if="item.header"
                                :key="item.header"
                                v-text="item.header"
                        ></v-subheader>

                        <v-divider
                                v-else-if="item.divider"
                                :key="index"
                                :inset="item.inset"
                        ></v-divider>

                        <v-list-item
                                v-else
                                :key="item.title"
                                @click="openChatWithUser(item.ids)"
                        >
                            <v-list-item-avatar>
                                <v-img :src="item.avatar"></v-img>
                            </v-list-item-avatar>

                            <v-list-item-content>
                                <v-list-item-title>
                                    {{item.title}}
                                    <span v-show="item.notReadMessages != 0" id="notReadMessagesNotification">{{item.notReadMessages}}</span>
                                </v-list-item-title>
                                <v-list-item-subtitle v-html="item.subtitle"></v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </template>
                </v-list>
            </v-col>
            <v-col class="text-center">
                <v-toolbar-title>{{eventsText}}</v-toolbar-title>
                <v-btn  color="primary" @click="getMessagingToken">
                    <p>uwu2</p>
                </v-btn>
            </v-col>
        </v-row>

    </v-container>
</template>

<script>
    import {mapActions, mapState, mapGetters} from 'vuex';
    import Constants from "../../store/Constants";
    import * as firebase from "../../../firebase";

    export default {
        name: "ChatHome",
        data() {
            return {
                chatsText: Constants.TEXT_CHATS
            }
        },
        methods: {
            ...mapActions([
                'prepareChatWithUser'
            ]),
            openChatWithUser(itemIDs) {
                this.$router.push('chats/chat/' + itemIDs.chat);
            },
            getMessagingToken () {
                firebase.messaging().getToken().then(async (token) => {
                    if (token) {
                        const currentMessageToken = window.localStorage.getItem('messagingToken')
                        console.log('token will be updated', currentMessageToken != token)
                        if(currentMessageToken != token){
                            await this.saveToken(token)
                        }
                    } else {
                        console.log('No Instance ID token available. Request permission to generate one.');
                        this.notificationsPermisionRequest();
                    }
                }).catch(function(err) {
                    console.log('An error occurred while retrieving token. ', err);
                });
            },
        },
        computed: {
            ...mapState({
                activeChat: state => state.activeChat,
                chats: state => state.chats,
            }),
            ...mapGetters({
                notReadMessagesCount: 'notReadMessagesCountByChatID'
            }),
            items: function () {
                let self = this;
                let itemsChatList = [];
                let sortedChats = self.chats.sort(function (chat1, chat2) {
                    if (chat1.messages.length > 0 && chat2.messages.length > 0) {
                        return chat2.messages[chat2.messages.length - 1].created.toDate().getTime() - chat1.messages[chat1.messages.length - 1].created.toDate().getTime();
                    } else {
                        return 0;
                    }
                });
                sortedChats.forEach(chat => {
                        let user = this.$store.getters.getChatUserByChatID(chat.id);
                        if (chat.messages.length > 0) {
                            itemsChatList.push({
                                ids: {
                                    chat: chat.id,
                                    user: user.id
                                },
                                avatar: user.profileImage,
                                title: user.username,
                                subtitle: chat.messages[chat.messages.length - 1].text,
                                notReadMessages: self.notReadMessagesCount(chat.id)
                            });
                            itemsChatList.push({divider: true, inset: true});
                        }
                    }
                )
                return itemsChatList;
            },
        }
    }
</script>

<style scoped>
    #notReadMessagesNotification {
        display: inline-block;
        background-color: lightskyblue;
        margin-left: 5px;
        padding: 4px;
        border-radius: 50%;
    }
</style>